import React from 'react'

import './Title.scss'
function Title() {

    return (
        <div className='index-title' >
            <div className='coordinate'>
                <div>
                    <i className='fa fa-map-marker'></i>1</div>
            </div>
            <div className='input '>
                <i className='fa fa-search'></i>
                <input readOnly disabled placeholder='' />
            </div>
        </div>
    )
}

export default Title 